@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

.container {
  width: 100%;
}

.terms-wrapper {
  padding: 2rem;
  color: rgba(0, 0, 0, 0.87);
}

.nav-wrapper {
  padding: 0 1rem;
}

.card-wrapper {
  color: #ffffff;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

.card-wrapper img {
  width: 100%;
}

.card-wrapper .content {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-wrapper .header {
  font-size: 2rem;
}

.floater-btn {
  position: fixed;
  right: -1rem;
  top: 45%;
  border-radius: 5rem 0 0 5rem;
  -webkit-animation: 2.5s ease 0s infinite beat;
          animation: 2.5s ease 0s infinite beat;
}

.footer-copyright {
  padding: 1rem;
}

@-webkit-keyframes beat {
  0%,
  50%,
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
  30%,
  80% {
    -webkit-transform: scale(0.92, 0.95);
            transform: scale(0.92, 0.95);
  }
}

@keyframes beat {
  0%,
  50%,
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
  30%,
  80% {
    -webkit-transform: scale(0.92, 0.95);
            transform: scale(0.92, 0.95);
  }
}

